.details-container {
    max-width: 600px;
    margin: 50px auto;
    text-align: center;
}

.details-container img {
    width: 100%; /* Make the image responsive */
    border-radius: 8px; /* Optional: rounded corners */
}

.details-container h2 {
    margin-bottom: 20px;
}

.details-container p {
    font-size: 1.1em;
}