.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background-color: #d51010;
  color: white;
  height: 60px; /* Adjust the height as needed */
}

.logo {
  display: flex;
  align-items: center;
}
.logo-icon {
  margin-right:10px;
  width: 45px; /* Adjust the size as needed */
  height: 60px; /* Adjust the size as needed */
}
.logo-text {
  margin-left: 20px;
  font-size: 1.5rem; /* Adjust the size as needed */
  color: white;
  text-decoration: none;
}

.nav {
  display: flex;
  justify-content: space-between; /* Space between logo and nav links */
  align-items: center; /* Center items vertically */
  background-color: #d51010;
}

.nav-links {
  margin-left: 800px; /* Push nav-links to the right */
  display: flex; /* Ensure links are in a row */
}

.nav-link {
  margin: 0 10px;
  color: rgb(255, 255, 255);
  text-decoration: none;
  padding: 10px 15px; /* Add padding for button-like appearance */
  border-radius: 5px; /* Rounded corners */
  transition: background-color 0.3s; /* Smooth transition for hover effect */
}

.nav-link:hover {
  background-color: #0056b3; /* Darker background on hover */
  text-decoration: none; /* Remove underline on hover */
}

.first-logo-nav-link {
  margin-left: 60px; /* Adjust the spacing as needed */
}

.logo-nav-link {
  margin-left: 20px; /* Adjust the spacing as needed */
}
.content-links {
  margin-left: 100px; /* Push nav-links to the right */
  display: flex; /* Ensure links are in a row */
}
.hero-section {
  position: relative;
  width: 100%;
  height: 200px; /* Adjust height as needed */
  overflow: hidden;
  margin-top: 20px; /* Add margin to create space below the nav bar */
}

.banner {
  width: 100%;
  height: 100%;
  background-size: contain; /* Change to 'contain' to ensure the entire image is visible */
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Prevent the image from repeating */
  display: flex;
  align-items: flex-end;
  justify-content: center;
  color: white;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
  transition: opacity 0.5s ease-in-out; /* Smooth transition */
}

.slide {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 2rem;
  transition: opacity 0.5s ease-in-out;
}



.prev-button {
  left: 10px;
  position: absolute;
  top: 50%;
  
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  border: none;
  padding: 10px;
  cursor: pointer;
}

.next-button {
  right: 10px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  border: none;
  padding: 10px;
  cursor: pointer;
}